/* Features Section */
.featuresSection {
  margin: 1.5rem 0;
}

/* Visually hidden heading for accessibility - maintains heading hierarchy */
.featuresHeading {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

.featureBadges {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  justify-content: center;
}

.featureBadge {
  display: inline-flex;
  align-items: center;
  padding: 0.625rem 1rem;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  border: 1.5px solid rgba(102, 126, 234, 0.2);
  border-radius: 20px;
  font-size: clamp(0.8125rem, 1vw + 0.4rem, 0.875rem);
  font-weight: 500;
  color: #667eea;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), background 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease;
  cursor: default;
  line-height: 1.4;
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-width: 100%;
  box-sizing: border-box;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
}

.featureBadge:hover {
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.15) 0%, rgba(118, 75, 162, 0.15) 100%);
  border-color: rgba(102, 126, 234, 0.4);
  transform: translate3d(0, -2px, 0);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.2);
  will-change: transform, box-shadow;
}

.featureBadge:not(:hover) {
  will-change: auto;
}

/* Mobile responsive styles */
@media (max-width: 767px) {
  .featureBadges {
    gap: 0.75rem;
    justify-content: center;
  }
  
  .featureBadge {
    padding: 0.625rem 1rem;
    font-size: 0.875rem;
  }
}

/* Tablet styles */
@media (min-width: 768px) and (max-width: 1023px) {
  .featureBadges {
    gap: 0.75rem;
  }
  
  .featureBadge {
    padding: 0.5625rem 0.9375rem;
    font-size: 0.8125rem;
  }
}

/* Desktop styles */
@media (min-width: 1024px) {
  .featureBadges {
    gap: 0.875rem;
  }
  
  .featureBadge {
    padding: 0.625rem 1rem;
    font-size: 0.875rem;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  /* Disable or minimize all transitions */
  *,
  *::before,
  *::after {
    transition-duration: 0.01ms !important;
  }

  /* Disable hover transforms */
  .featureBadge:hover {
    transform: none !important;
  }

  /* Keep will-change disabled */
  .featureBadge {
    will-change: auto !important;
  }
}
